@font-face {
  font-family: 'Tilt Neon';
  src: url(fonts/TiltNeon-Regular.ttf);
}
body{
  margin: 0 !important;
  padding: 0 !important;
  font-family: Tilt Neon, sans-serif;
  background-color: rgb(35, 29, 58);
  color: white;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  font-size: 21;
  transition: .25s ease-in-out;
}
nav{
  display: grid;
  grid-template-columns: 20% 20% 20% 20% 20%;
  text-align: center;
  align-items: center;
}
article{
  padding-top: 60px;
}
.tab{
  text-decoration: none;
  color: white;
  height: 100%;
  display: grid;
  align-items: center;
}
.tab:hover{
  color: cyan;
  background-color: black;
}
header{
  position: sticky;
  top: 0;
  display: grid;
  
  font-weight: bold;
  font-size: 20px;
  color: white;
  background-color: rgb(16, 13, 29);
  box-shadow: 0px 0px 10px black;
  width: 100%;
  z-index: 1;
  user-select: none;
}
header > p{
  display: none;
}
.About{
  user-select: none;
  display: grid;
  column-gap: 10%;
  padding-top: 0px;
  text-align: center;
}
.AboutImage{
  width: 100%;
}
.AboutDetails{
  align-self: center;
}
.SkillList{
  display: grid;
  gap: 25px;
  text-align: center;
}
.SkillList > i {
  width: 95%;
  margin: 0 auto;
}
.title {
  margin-left: 2%;
	text-decoration: none; 
	position: relative;
  width: max-content;
  user-select: none;
  text-shadow: 4px 4px 4px black;
}   

.title:after {
  position: absolute;
  content: '';
  height: 8px;
  bottom: -5px; 
  left: 0px;
  width: 130%;
  box-shadow: 4px 4px 4px black;
}
.SkillsTitle:after{
  background: linear-gradient(to right, red, orange);
}
.ProjectsTitle:after{
  background: linear-gradient(to right, orange, purple);
}
.ResumeTitle::after{
  background: linear-gradient(to right, purple, rgb(76, 0, 255));
}
.ContactsTitle:after{
  background: linear-gradient(to right,rgb(76, 0, 255), rgb(0, 255, 30) );
}
.Resume , .Contacts{
  text-align: center;
}
.DownloadButton , button{
  background-color: rgb(162, 0, 255);
  padding: 20px;
  font-weight: bold;
  font-size: 20px;
  text-decoration: none;
  color: white;
  text-align: center;
  border: 2px white solid;
  border-radius: 15px;
}
.DownloadButton > p{
  width: fit-content;
  padding: 25px;
}
.DownloadButton:hover , button:hover{
  filter: brightness(75%);
}
a, .ContactLink{
  color: white;
  text-decoration: none;
}
.ContactLink:hover{
  text-shadow: 0px 0px 5px white;
}
.ContactsEmail{
  display: grid;
}
.ContactsEmailInfo{
  margin-left: 20px;
  margin-bottom: 20px;
  text-align: center;
  display: flex;
  flex-flow: column;
  row-gap: 10px;
  justify-content: center;
  height: 80%;
}
.ProjectCollection{
  display: grid;
  grid-template-columns: 1fr;
  gap: 40px;
  margin: 5%;
}
.Project{
  text-align: center;
  background-color: rgb(16, 13, 29);
  transition: .2s ease-in-out;
  position: relative;
  overflow: hidden;
  border-radius: 25px;
  box-shadow: 0px 0px 10px black;
}
.ProjectButton{
  position: absolute;
  right: 10px;
  top: 0px;
  background-color: rgb(16, 13, 29);
  padding: 5px;
  font-size: 16px;
  border-radius: 10px;
  border: 2px solid white;
  transition: .25s ease-in-out;
}
.ProjectButton:hover{
  background-color: black;
  border-radius: 0px;
}
.Project:hover{
  filter: brightness(70%);
}
.ProjectImage{
  width: 100%;
  aspect-ratio: 3 / 1.5;
}
.ProjectFooter{
  width: 95%;
  display: grid;
  margin: 0 auto;
  align-items: center;
}
.ProjectFooterQRCode{
  display: none;
  width: 80%;
}
.ProjectFooterText{
  text-align: center;
  padding-bottom: 20px;
}
dialog{
  width: 80%;
  border-radius: 20px;
  box-shadow: 0px 0px 10px black;
  color: white;
  background-color: rgb(16, 13, 29);
}
@media (min-width:961px) {
  body{
    font-size: 25px;
  }
  .ContactsEmail{
    grid-template-columns: 2fr 3fr;
  }
  .headerName{
    display: block;
    margin-left: 20px;
  }
  header{
    grid-template-columns: 50% 50%;
  }
  .ProjectCollection{
    grid-template-columns: 50% 50%;
  }
  .ProjectFooter{
    grid-template-columns: 3fr 1fr;
  }
  .ProjectFooterQRCode{
    display: block;
  }
  .ProjectFooterText{
    text-align: left;
  }
  .About{
    grid-template-columns: 45% 45%;
    text-align: left;
  }
  .SkillList{
    grid-template-columns: 1fr 2fr;
  }
  .title{
    margin-left: 1%;
  }
  .ContactsEmailInfo{
    margin-left: 20px;
    text-align: left;
    display: flex;
    flex-flow: column;
    row-gap: 50px;
    justify-content: center;
    height: 80%;
  }
}